
import {
  NgModule,
  Component,
  Directive,
  ElementRef,
  Input,
  AfterViewInit
} from "@angular/core";
import { getAttributes } from "@carbon/icon-helpers";

@Component({
	selector: "ibm-icon-watson-health-window-overlay20",
  template: `
    <svg
      ibmIconWatsonHealthWindowOverlay20
      [ariaLabel]="ariaLabel"
      [ariaLabelledby]="ariaLabelledby"
      [ariaHidden]="ariaHidden"
      [title]="title"
      [isFocusable]="focusable"
      [attr.class]="innerClass">
    </svg>
  `
})
export class WatsonHealthWindowOverlay20 {
  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() focusable: boolean = false;
  @Input() innerClass: string;
}

@Directive({
  selector: "[ibmIconWatsonHealthWindowOverlay20]"
})
export class WatsonHealthWindowOverlay20Directive implements AfterViewInit {
  static titleIdCounter = 0;

  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() isFocusable: boolean = false;

  constructor(protected elementRef: ElementRef) {}

	ngAfterViewInit() {
    const svg = this.elementRef.nativeElement;
    svg.setAttribute("xmlns", "http://www.w3.org/2000/svg");

    const domParser = new DOMParser();
    const rawSVG = `<svg focusable="false" preserveAspectRatio="xMidYMid meet" style="will-change: transform;" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 32 32" aria-hidden="true"><path d="M15 6H17V9H15zM25 17H28V19H25zM15 27H17V30H15zM4 17H7V19H4z"></path><path d="M7.55 9.03H9.55V12.03H7.55z" transform="rotate(-45 8.558 10.545)"></path><path d="M21.96 9.54H24.96V11.54H21.96z" transform="rotate(-45 23.466 10.54)"></path><path d="M22.46 23.94H24.46V26.94H22.46z" transform="rotate(-45 23.459 25.443)"></path><path d="M7.04 24.45H10.04V26.45H7.04z" transform="rotate(-45 8.55 25.448)"></path><path d="M4 2H28V4H4zM16 24a6 6 0 1 0-6-6A6 6 0 0 0 16 24zm0-10v8a4 4 0 0 1 0-8z"></path></svg>`;
    const svgElement = domParser.parseFromString(rawSVG, "image/svg+xml").documentElement;

    let node = svgElement.firstChild;
    while (node) {
      // importNode makes a clone of the node
      // this ensures we keep looping over the nodes in the parsed document
      svg.appendChild(svg.ownerDocument.importNode(node, true));
      node = node.nextSibling;
    }

    const attributes = getAttributes({
      width: 20,
      height: 20,
      viewBox: "0 0 32 32",
      title: this.title,
      "aria-label": this.ariaLabel,
      "aria-labelledby": this.ariaLabelledby,
      "aria-hidden": this.ariaHidden,
      focusable: this.isFocusable.toString()
    });

    const attrKeys = Object.keys(attributes);
    for (let i = 0; i < attrKeys.length; i++) {
      const key = attrKeys[i];
      const value = attributes[key];
      if (key === "title") {
        continue;
      }
      if (value) {
        svg.setAttribute(key, value);
      }
    }

    if (attributes.title) {
      const title = document.createElement("title");
      title.textContent = attributes.title;
      WatsonHealthWindowOverlay20Directive.titleIdCounter++;
      title.setAttribute("id", `watson-health-window-overlay20-${WatsonHealthWindowOverlay20Directive.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `watson-health-window-overlay20-${WatsonHealthWindowOverlay20Directive.titleIdCounter}`);
    }
	}
}

@NgModule({
  declarations: [
    WatsonHealthWindowOverlay20,
    WatsonHealthWindowOverlay20Directive
  ],
  exports: [
    WatsonHealthWindowOverlay20,
    WatsonHealthWindowOverlay20Directive
  ]
})
export class WatsonHealthWindowOverlay20Module {}
